html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-size: inherit;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

ul {
  list-style: none;
}

* {
  font-family: "Poppins", sans-serif;
  color: #F4F4F4;
  box-sizing: border-box;
}

body {
  background-color: #1D1D1D;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}

.header {
  margin-top: 30px;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.header__icon {
  height: 48px;
}
.nav {
  width: 100%;
  max-width: 618px;
  display: flex;
  justify-content: space-between;
  align-items: bottom;
}
.nav__item {
  font-size: 16px;
  line-height: 100%;
  text-transform: uppercase;
  align-self: flex-end;
}

.info {
  margin: 120px auto 90px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.info__job {
  font-size: 24px;
  font-weight: 400;
  line-height: auto;
  letter-spacing: 5%;
  color: #616279;
}
.info__title {
  font-family: "Playfair Display", serif;
  color: #F4F4F4;
  margin-bottom: 25px;
  font-size: 80px;
  font-weight: 600;
  line-height: 70px;
  text-align: center;
  letter-spacing: 0.05em;
  max-width: 606px;
}
.info__text {
  font-size: 16px;
  line-height: auto;
  width: 100%;
  max-width: 306px;
  margin-bottom: 30px;
  letter-spacing: 0.05em;
  text-align: center;
}
.info__bottom {
  border: 1px solid #F4F4F4;
  text-align: center;
  vertical-align: center;
  padding: 5px;
  border-radius: 50px;
  color: #F4F4F4;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.05em;
  width: 230px;
  height: 50px;
  padding: 16px 55px 17px;
}

.contacts {
  display: flex;
  justify-content: space-between;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */
  text-transform: uppercase;
  color: #F4F4F4;
}
.contacts__social {
  display: flex;
  justify-content: space-between;
  width: 160px;
}
.contacts__social-link {
  text-decoration: none;
}
.contacts__telephone {
  text-decoration: none;
}