@import 'reset';



* {
  font-family: 'Poppins', sans-serif;
  color: #F4F4F4;
  box-sizing: border-box;
}

body {
  background-color: #1D1D1D;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}

.header {
  margin-top: 30px;

  &__inner {
    display: flex;
    justify-content: space-between;

    width: 100%;

  }

  &__icon {
    height: 48px;
  }

  &__icon-img {}
}

.nav {
  width: 100%;
  max-width: 618px;
  display: flex;
  justify-content: space-between;
  align-items: bottom;

  &__item {
    font-size: 16px;
    line-height: 100%;
    text-transform: uppercase;
    align-self: flex-end;
  }
}

.info {
  margin: 120px auto 90px auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__job {
    font-size: 24px;
    font-weight: 400;
    line-height: auto;
    letter-spacing: 5%;
    color: #616279;
  }

  &__title {
    font-family: 'Playfair Display', serif;
    color: #F4F4F4;
    margin-bottom: 25px;
    font-size: 80px;
    font-weight: 600;
    line-height: 70px;
    text-align: center;
    letter-spacing: 0.05em;
    max-width: 606px;
  }

  &__text {
    font-size: 16px;
    line-height: auto;
    width: 100%;
    max-width: 306px;
    margin-bottom: 30px;
    letter-spacing: .05em;
    text-align: center;
  }

  &__bottom {
    border: 1px solid #F4F4F4 ;
    text-align: center;
    vertical-align: center;
    padding: 5px;
    border-radius: 50px;
    color: #F4F4F4;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: .05em;
    width: 230px;
    height: 50px;
    padding: 16px 55px 17px;
  }
}

.contacts {
 display: flex;
 justify-content: space-between;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */

  text-transform: uppercase;
  color: #F4F4F4;

  &__social {
    display: flex;
    justify-content: space-between;
    width: 160px;
    
  }

  &__social-link {
    text-decoration: none;
  }

  &__telephone {
    text-decoration: none;
  }
}